@media screen and (max-width: 1300px) {
  .container {
    width: 1100px;

    &__process {
      &-white {
        padding-bottom: 291px;
        padding-top: 50px;
        margin: 0 25px;
      }
    }

    &__features {
      padding-top: 50px;
      padding-bottom: 10px;
    }
  }

  .header {
    &__photo {
      width: 850px;
    }
  }

  .process {
    &__box-left {
      position: absolute;
      top: 12em;
      left: 2em;
      z-index: 1;
    }

    &__box-right {
      position: absolute;
      top: 4em;
      left: 19em;
      z-index: 1;
    }
  }

  .features {
    &__right {
      width: 600px;
      height: 787px;
    }
  }

  .testimonials {
    &__items {
      width: 930px;
    }

    &__item {
      width: 280px;
      padding: 20px 0 30px;
    }
  }

  .item {
      &__description {
      height: 125px;
      margin-bottom: 15px;

      &::after {
        left: 36%;
      }
    }
  }

  .image__background {
    margin: 40px 0 80px 30px;
  }
}

@media screen and (max-width: 1200px) {
  .container {
    width: 900px;

    &__testimonials {
      padding-top: 70px;
    }
  }

  .button__slider {
    top: 620px;
    left: 350px;
  }

  .what-we-do {
    &__item {
      max-width: 244px;
    }
  }

  .testimonials {
    &__items {
      width: 870px;
    }

    &__item {
      width: 230px;
      padding: 20px 0 30px;
    }
  }
}

@media screen and (max-width: 980px) {
  .container {
    width: 800px;

    &__process {
      &-white {
        padding-bottom: 345px;
      }
    }
  }

  .header {
    &__photo {
      height: 640px;
      width: 800px;
    }
  }

  .what-we-do {
    flex-wrap: wrap;
    justify-content: center;

    &__item {
      max-width: 234px;
      margin-left: 50px;
      margin-bottom: 10px;
    }
  }

  .process {
    &__box {
      width: 170px;
      height: 256px;
    }

    &__box-left {
      top: 17em;
    }

    &__box-right {
      top: 12em;
      left: 15em;
    }

    &__box_1,
    &__box_2 {
      margin: 0 15px 25px 0;
    }
  }

  .box__content {
    padding: 40px 60px 32px 60px;
  }

  .number-img {
    top: -3em;
    left: 45%;
  }

  .testimonials {
    &__items {
      width: 780px;
    }

    &__item {
      width: 230px;
      padding: 20px 0 30px;
    }
  }

  .communication {
    display: grid;
    grid-template-columns: 1fr 300px;
  }

  .send-us {
    margin-right: 60px;
  }
}

@media screen and (max-width: 940px) {
  .header__social-icons {
    position: absolute;
    display: flex;
    justify-content: space-between;
    transform: rotate(90deg);
    top: 310px;
    right: -220px;
  }
}

@media screen and (max-width: 880px) {
  .container {
    width: 600px;

    &__welcome {
      width: 600px;
    }

    &__vpr {
      max-width: 550px;
    }

    &__vpr-wrapp {
      margin-top: 0;
    }

    &__testimonials {
      padding-bottom: 40px;
      background: #fff;
    }
  }

  .button__slider {
    top: 620px;
    left: 250px;
  }

  .who-we {

   &__title {
     font-size: 44px;
   }

   &__paragraph {
     font-size: 18px;
     padding-bottom: 32px;
   }
  }

  .header {
    &__photo {
      width: 680px;
    }

    &__nav {
      display: grid;
      grid-template-columns: 15% 1fr 15%;
      padding-bottom: 85px;
    }
  }

  .process {
    &__box {
      width: 150px;
      height: 275px;
    }

    &__box-left {
      display: flex;
      top: 24em;
      left: -24em;
    }

    &__box-right {
      display: flex;
      top: 24em;
      left: -4em;
    }

    &__box_1,
    &__box_2 {
      margin: 0 10px 10px 0;
    }
  }

  .box__content {
    padding: 40px 60px 32px 50px;
  }

  .number-img {
    left: 44%;
  }

  .features {
    &__right {
      width: 500px;
      height: 749px;
    }

    &__title {
      padding-bottom: 30px;
    }
  }

  .testimonials {
    &__items {
      padding-top: 60px;
      width: 630px;
      position: static;
      flex-wrap: wrap;
      justify-content: center;
    }

    &__item {
      margin: 0 40px 40px 0;
    }
  }

  .vpr {
    padding-top: 90px;
  }

  .button_media {
    display: flex;
    justify-content: center;
  }

  .send-us {
    text-align: center;
    margin-right: 0;
  }

  .contact-us {
    padding-top: 50px;
    text-align: center;
  }

  .social-icons {
    max-width: 100%;
    justify-content: center;
  }

  .social-icon {
    padding-right: 48px;
  }

  .communication {
    display: block;
  }

  .footer {
    display: block;

    &__author {
      text-align: center;
      padding-bottom: 10px;
    }

    &__nav {
      width: 100%;
    }
  }
}

