    &__box {
      width: 100%;
      height: 180px;
    }

    &__box-left {
      flex-wrap: wrap;
      position: static;
      padding: 10px 0;
    }

    &__box-right {
      flex-wrap: wrap;
      position: static;
    }

    &__box_1,
    &__box_2 {
      margin: 0 0 10px 0;
    }
  }

  .box__content {
    padding: 40px 60px 32px 45%;
  }

  .number-img {
    left: 49%;
  }

  .testimonials {
    &__items {
      width: 450px;
    }

    &__item {
      width: 200px;
      margin: 0 25px 20px 0;
    }
  }

